@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    font-family: "Jost", sans-serif;
    font-family:Arial, Helvetica, sans-serif;
}

body {
    background: #f3f2ef;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "Jost", sans-serif;
}

html {
    font-size: 100%;
    height: 100%;
}

html {
    background: rgb(218, 213, 213);
}

.box {
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background-image: url(/static/media/wthback.9ba04643.jpg);
    position: relative;
    overflow: hidden;
    -webkit-filter: drop-shadow(8px  8px 10px gray);
            filter: drop-shadow(8px  8px 10px gray);
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 35rem;
}

.wave {
    opacity: 0.3;
    position: absolute;
    top: 85%;
    left: 35%;
    background: rgb(0, 0, 0);
    width: 70rem;
    height: 70rem;
    margin-left: -25rem;
    margin-top: -20rem;
    -webkit-transform-origin: 50% 48%;
            transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    z-index: 1;
}

.wave.-three {
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.3;
}

.wave.-two {
    -webkit-animation: drift 8000ms infinite linear;
            animation: drift 8000ms infinite linear;
    opacity: 0.3;
    z-index: 3 !important;
}

.box::after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

@-webkit-keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.info {
    margin-top: 45px;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}

.location {    
    margin-top: -4.5rem;
    text-align: center;
    font-weight: 900;
    font-size: 2rem;
    font-stretch: expanded ;
    color: rgb(255, 255, 255);
    text-transform: capitalize;

}

.fa-street-view {
    -webkit-animation: rotates 3s linear infinite ease;
            animation: rotates 3s linear infinite ease;
}

@-webkit-keyframes rotates {
    from {
        -webkit-transform: translateX(-0.5rem);
                transform: translateX(-0.5rem);
    }
    from {
        -webkit-transform: translateX(0.5rem);
                transform: translateX(0.5rem);
    }
}

@keyframes rotates {
    from {
        -webkit-transform: translateX(-0.5rem);
                transform: translateX(-0.5rem);
    }
    from {
        -webkit-transform: translateX(0.5rem);
                transform: translateX(0.5rem);
    }
}

.temp {
    margin-top: 2rem;
    text-align: center;
    font-weight: 700;
    font-size: 3rem;
    color :#ffffff;
}

.tempmin_max {
    text-align: center;
    margin-top: 12rem;
    font-weight: 300;
    font-size: 1.2rem;
    color :#ffffff;
}

@-webkit-keyframes fas-anime {
    from {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    to {
        -webkit-transform: scale(1.5);
                transform: scale(1.5)
    }
}

@keyframes fas-anime {
    from {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    to {
        -webkit-transform: scale(1.5);
                transform: scale(1.5)
    }
}

@media (max-width: 600px) {
    .box {
        width: 90vw;
        height: 80vh;
    }

    .wave {
        top: 80%;
    }

    
    .info {
        font-size: 1.5em;
    }
}

@media (max-width: 500px) {
    .box {
        height: 80vh;
    }

    .wave {
        top: 100%;
    }
}

body > span {
    width: 100vw;
    text-align: center;
    color: grey;
}

.inputData {
    width: 100%;
    height: 8.5vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 4em;
}

.inputField {
    height: 50%;
    width: 55%;
    margin: auto;
    border-radius: 0.5rem;
    border: 0ch;
    box-shadow: 10px 20px 20px #5d5f64;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.1rem;
}

.errorMsg {
    text-align: center;
    font-size: 2rem;
    font-weight: 800;
    margin: 2rem auto;
    color :#ffffff;
}
